//_____________ INPORTS
@import "_fonts";
// @import ./materialize/materialize
// @import ./bootstrap/bootstrap-grid

//_____________ FONTS
$medium: GothamProMedium;
$lights: GothamProLights;
$black: GothamProBlack;
$awe: fontawesome;

//_____________ CONST
$accent: #0065ab;
$sub-accent: #8ac0e4;
$cyan: #e6eff6;
$header: #3c4348;
$font: #363636;
$font-gray: #5d5a63;
$font-dark: #707070;
$border: #808285;
$bg: #f5f5f5;

//_____________ SETTINGS
html {
	font-size: 18px; }
body {
	min-width: 320px;
	position: relative;
	line-height: 1.5;
	overflow-x: hidden;
	font-size: 1rem;
	opacity: 1;
	font-family: $lights;
	font-weight: 500;
	overflow-scrolling: touch;
	margin: 0; }
a:hover, a:focus {
	text-decoration: none; }
a {
	color: inherit;
	text-decoration: none; }
input:hover, input:focus, a:focus {
	outline: none; }
@media (min-width: 1170px) {
	.container {
		max-width: 1170px; } }

//_____________ B U T T O N S
.btn__agency {
	font-size: 1.55rem;
	font-family: $medium;
	background-color: $accent;
	color: #ffffff;
	text-transform: uppercase;
	display: block;
	text-align: center;
	padding: 10px 0;
	white-space: nowrap;
	&:hover {
		color: #ffffff; } }
.btn__agency.small {
	font-size: 1rem; }
.btn__agency.white {
	font-size: .88rem;
	color: $font-gray;
	background-color: transparent;
	border: 1px solid $border;
	font-family: $medium;
	padding: 9px 0; }
.btn__agency.white.phone {
	font-family: $lights;
	font-size: 1.1rem;
	padding: 6px 0; }
//_____________ H E A D E R S
h1 {
	font-family: $black;
	font-size: 2.83rem;
	color: $accent;
	line-height: 1.15;
	text-transform: uppercase;
	strong {
		font-size: 3.22rem; } }
h2.subtitle {
	font-size: 2.23rem;
	text-transform: none; }
h2 {
	color: $accent;
	font-family: $black;
	font-size: 2.45rem;
	text-transform: uppercase; }
h3 {
	font-family: $medium;
	font-size: 1.67rem;
	text-transform: none;
	color: $accent; }


//_____________ H E A D E R
.header {
	background-image: url("../img/header_bg.jpg");
	background-repeat: no-repeat;
	background-position: bottom right;
	margin-top: 84px;
	&__buttons {
		padding-top: 13px;
		& > a {
			margin-bottom: 18px; } }
	&__content {
		margin-top: 126px; }
	&__action {
		margin-top: 113px;
		padding-bottom: 46px;
		text-align: center;
		h3 {
			margin-bottom: 36px; } } }
@media (max-width: 1400px) {
	.header {
		background-size: 48%; } }
//_____________ P R I C E
.price {
	margin-top: 102px;
	text-align: center;
	padding-bottom: 106px;
	h2 {
		margin-bottom: 0; }
	h2.subtitle {
		color: $sub-accent;
		font-size: 1.67rem; }
	&__form {
		&--items {
			margin-top: 53px; }
		&--item {
			display: flex;
			align-items: center;
			justify-content: center;
			a.btn__agency {
				flex: 100% 0 0;
				font-size: 1rem; }
			&--field {
				flex-grow: 1;
				border: 1px solid $font-gray;
				font-family: $medium;
				text-align: left;
				padding: 6px 0 7px 20px;
				&#budget {
					&::after {
						content: 'рублей';
						padding-left: 5px; } } }
			&--label {
				color: $font-gray;
				padding-right: 69px; }
			input {
				margin: 22px 0;
				width: 100%;
				appearance: none;
				background-image: url("../img/range_bg.png");
				background-repeat: no-repeat;
				background-position: center;
				background-size: calc(100% - 33px) auto;
				&::-webkit-slider-thumb {
					appearance: none;
					width: 33px;
					height: 33px;
					background-color: #ffffff;
					border: 1px solid $font-gray;
					border-radius: 50%; }
				&::thumb {
					background-color: #fff; } } } } }
